import * as React from "react";
import NavHeader from "../../components/NavHeader";
import { connect } from "react-redux";
import "./index.less";
import { Link } from "react-router-dom";
import { Store } from "../../types";
import actions from "../../store/actions/session";
declare function require(url: string): string;
let profile = require("../../images/profile.png");
interface IProps {
  history: any;
  login: any;
}
//match history location Route
class Login extends React.Component<IProps> {
  state = {
    username: "",
    passowrd: ""
  };
  handleLogin = () => {
    let user = this.state;
    this.props.login(user);
  };
  render() {
    return (
      <div className="login">
        <NavHeader title="" history={this.props.history} />
        <div className="login-logo">
          <img src={profile} />
        </div>
        <input
          onChange={event =>
            this.setState({
              username: event.target.value
            })
          }
          type="text"
          name="username"
          placeholder=""
        />
        <input
          onChange={event =>
            this.setState({
              passowrd: event.target.value
            })
          }
          type="text"
          name="password"
          placeholder=""
        />
        <Link to="/reg"></Link>
        <button onClick={this.handleLogin}>&nbsp;</button>
      </div>
    );
  }
}
export default connect((state: Store) => state.session, actions)(Login);
